
$antra: rgb(29, 31, 33);

.appBody{

  border: 3px solid black;
  border-radius: 2%;
  width: 70vw;
  background-image: linear-gradient(to bottom, rgba($antra, 0.8), rgba($antra, 0.8)), url("../imgs/hintergrund.jpg");
  box-shadow: 5px 5px 15px 5px #000000;
  opacity: 01;
 
}

.mainBackGround{
  margin-top: 30vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  .fullLogo{
    margin-top: 20vh;
    height: 20vh;
    width: auto;
    margin-bottom: 5vh;
    object-fit: contain;
  }
}




