$red: #ff3333;

@font-face {
    font-family: 'PotatoFont'; /*a name to be used later*/
    src: url('../fonts/Potato\ Chips.ttf'); /*URL to font*/
}

.navLogo{
    position: fixed;
    top:1vh;
    left:5vh;
    height: 10vh;
    z-index: 11;
}


.navContainer{
    
    i{
        color: $red;
        font-size: 30px;
        position: fixed;
        top:3.3vh;
        right:5vh;
        background-color: black;
        padding: 20px;
        border-radius: 50%;
        z-index: 11;
    }
}

.nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    padding: 30px;
    background: rgba($color: #1e2324, $alpha: 0.95);
    color: seashell;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-shadow: 5px 5px 15px 5px #000000;
    z-index: 10;

    .button {
        width: 12vw;
        margin-left: 50px;
        margin-right: 50px;
        position: relative;
        color: black;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 34px;
        display: flex;
        justify-content: center;
        padding: 10px 15px;
        letter-spacing: 1px;
        overflow: hidden;
        transition: 0.5s;
        color: $red;
        font-family: PotatoFont;
    }
}

.nav_section {
    display: flex;
    align-items: center;
    padding: 0;
}

.nav_item {

    font-size: 20px;
    margin-left: 10px;
}

.navlogo img {
    width: 260px;
    height: 150px;
    padding-left: 0 px;
}

.button_line {
    position: absolute;
    display: block;
}

.button_line-top {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, $red);
}
.button:hover{
    transition: all;
    color:$red;
}
.button:hover .button_line-top {
    left: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

.button_line-bottom {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, $red);
}

.button:hover .button_line-bottom {
    right: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

.button_line-left {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(0deg, transparent, $red);
}

.button:hover .button_line-left {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0s;
}

.button_line-right {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(1800deg, transparent, $red);
}

.button:hover .button_line-right {
    top: 100%;
    transition: 1s;
    transition-delay: 0s;
}

